.contact {
  @include span-columns(12);
  background: url('/assets/images/bg_contact.jpg') $radical-red;
  background-size: cover;
  min-height: rem(600);
  padding: rem(40) 0;

  &__form {
    font-size: rem(33);
    color: $sea-shell;
    text-align: center;
    font-weight: 300;

    @include media($screen-small) {
      font-size: rem(16);
    }

  }

}
