*:focus {
    outline: none;
}

.form {

  %inputs-style {
    border: 0;
    border-bottom: 1px dashed $peach;
    color: $peach;
    font-style: italic;
    font-weight: 200;
    text-align: center;

    &:focus {
      border-bottom: 1px dashed lighten($peach, 10%);
    }

  }

  &__field {
    @extend %inputs-style;
    display: inline-block;
    width: auto;

    @include placeholder {
      color: $peach;
      opacity: 1 !important;
    }
  }

  &__textarea {
    @extend %inputs-style;
    width: 100%;

    @include placeholder {
      color: $peach;
      opacity: 1 !important;
    }
  }

  &__btn {
    @include size(81px 66px);
    background: url("../images/send_icon.png") no-repeat transparent top center !important;
    margin-top: rem(40);
  }

}
