.about {
  @include span-columns(12);
  background: url('/assets/images/bg_about.jpg') $oxford-blue;
  background-size: cover;
  min-height: 100vh;
  text-align: center;

  @include media($screen-small) {
    padding: rem(40) 0;
  }


  &__col {
    @include span-columns(6 f 12);
    text-align: center;

    @include media($screen-small) {
      @include span-columns(12);
    }

    &--text {
      text-align: left;
    }

    img {
      max-width: 100%;
      @include pad(rem(30));

      @include media($screen-small) {
        width: rem(300);
         @include pad(0);
      }

    }
  }

  &__title {
    color: $sea-shell;
    font-size: rem(34);
    margin-top: rem(80);

    &--dot {
      color: $radical-red;
    }
  }

  &__text {
    color: $sea-shell;
    font-weight: 200;
    font-size: rem(22);

    @include media($screen-small) {
      font-size: rem(17);
    }

  }


}
