.centered-box {
  display: table;
  table-layout: fixed;
  width: 100%;

  &__content {
    display: table-cell;
    vertical-align: middle;
    width: 100%;
    height: 100%;
  }

}

.wrapper {
  @include outer-container();

  @include media($screen-medium) {
    padding: $base-spacing;
  }

  @include media($screen-small) {
    padding: $small-spacing;
  }

}

* {
  @include transition(all .4s);
}
