.main-header {
  @include pad(rem(40));
  @include position(absolute, 0 null null 0);
  @include span-columns(12);
  z-index: 1;

  @include media($screen-small) {
    @include pad($small-spacing);
  }

  &--fixed {
    @include pad(rem(5));
    @include position(fixed, 0 null null 0);
    background: rgba($oxford-blue, .9);
    z-index: 2;

    // mobile
    @include media($screen-min-small) {
      @include pad(rem(10) rem(40));
      @include position(fixed, 0 null null 0);
      background: rgba($oxford-blue, .9);

      .nav {
        line-height: rem(60);
      }

    }

    .main-header__brand > svg {
      @include size(60px);
    }
  }

  &__brand {
    width: rem(100);
    float: left;

    svg {
      @include media($screen-small) {
        padding: rem(5);
        @include size(50px auto);
      }
    }
  }

  &__icon {
    display: none;

    @include media($screen-small) {
      display: block;
      float: right;
      position: relative;
    }
  }

}

//navigation menu
.nav {
  @include span-columns(8 of 12);
  float: right;
  text-align: right;
  text-transform: uppercase;

  &__item {
    display: inline-block;

    @include media($screen-small) {
      display: block;
      padding: 0;
    }
  }

  &__link {
    padding: 0 rem(40);
    transition: all .2s;

    @include media($screen-medium) {
      padding: 0 rem(20);
    }

  }

  &__link:link,
  &__link:visited  {
    color: $sea-shell;
    font-family: $base-font-family;
    font-size: rem(19);
    font-weight: 300;
  }

  &__link:hover,
  &__link:active{
    color: $peach;
    font-weight: 300;
  }
}

// mobile menu
.nav--mobile {
  @include media($screen-small) {
    position: fixed;
    top: 0;
    left: -100%;
    z-index: 1000;
    background: rgba($oxford-blue, .9);
    margin-top: 0;
    height: 100%;
    overflow: auto;
    padding: rem(40) rem(20);
  }

  .nav__item {
    @include media($screen-small) {
      @include padding(rem(20) 0);
    }
  }

}

.nav--is-open {
  left: 0;
}
