.main-footer {
  @include span-columns(12);
  background:  $oxford-blue;
  padding: rem(20) 0;

  &__col {
    @include span-columns(6 of 12);
    color: $sea-shell;
    font-size: rem(14);
    font-weight: 100;

    @include media($screen-small) {
      @include span-columns(12);
      text-align: center;
    }

    &--2 {
      text-align: right;
    }
  }

  a {
    $action-color: $radical-red;
    color: $action-color;
    text-decoration: underline;
  }
}
