.intro {
  @include span-columns(12);
  background: url('/assets/images/bg_home.jpg') $oxford-blue;
  background-size: cover;
  min-height: 100vh;
  text-align: center;

  &__gon > svg{
    @include media($screen-small) {
      width: rem(300);
      margin: 0 auto;
    }

    @include media($screen-tiny) {
      width: rem(200);
      margin: 0 auto;
    }

  }

  &__hashtags {
    font-family: $base-font-family;
    font-size: rem(20);
    color: $sea-shell;
    font-weight: 100;
    line-height: rem(60);
    @include fill-parent;

    @include media($screen-small) {
      font-size: rem(17);
      line-height: rem(30);
    }
  }

  &__arrow {
    @include position(absolute, null calc(50% - 18.5px) 40px null);
  }

}

