.works {
  @include span-columns(12);
  background:  $sea-shell;
  min-height: 400px;

  &__heading {
    @include fill-parent;
    background: url('/assets/images/bg_last_works@2x.jpg') $radical-red;
    background-size: cover;
    min-height: rem(250);
    padding-top: rem(90);
    text-align: center;

    @include media($screen-small) {
      min-height: rem(90);
      padding-top: rem(30);
    }

  }

  &__title {
    color: $sea-shell;
    font-size: rem(50);

    @include media($screen-small) {
      font-size: rem(25);
    }

  }

  &__col {
    @include span-columns(4 of 12);
    margin-bottom: rem(30);
    min-height: 100px;

    @include media($screen-small) {
      @include span-columns(12);
      margin: 0;
    }
  }

  &__box-big {
    @include fill-parent;
    @include transition(all .4s);
    float: left;
    position: relative;

    @include media($screen-medium) {
      height:auto;
    }

  }

  &__box-small {
    @include fill-parent;
    @include transition(all .4s);
    float: left;
    position: relative;

    @include media($screen-medium) {
      height:auto;
    }

  }

  &__box-big:hover,
  &__box-small:hover {
    .works__info {
      opacity: 1;
    }
  }

  &--margin {
    margin-bottom: rem(30);
  }

  &__thumb {
  }

  &__info {
    @include position(absolute, 0 null null 0);
    @include size(100%);
    background: rgba($oxford-blue, .9);
    vertical-align: middle;
    z-index: 1;
    opacity: 0;
  }

}



// thumbs home
.job {
  @include position(absolute, null null 0 0);
  @include size(100% auto);
  padding: rem(30);

  &__title {
    color: $peach;
    display: block;
    font-size: rem(24);
    font-weight: 700;
  }

  &__categories {
    color: $sea-shell;
    display: block;
    font-weight: 100;
    font-size: rem(18);
  }

  &__btn {
    color: $green-light;
    border: 2px solid $green-light;
    padding: rem(20);
    font-size: rem(18);
    text-decoration: underline;
    text-transform: uppercase;
    float: left;
    font-weight: 500;
    margin-top: rem(23);

    &:hover {
      color: $sea-shell;
    }
  }
}
