.case {

  &__intro {
    @include span-columns(12);
    background: $oxford-blue;
    min-height: rem(380);
    padding-top: rem(130);
    position: relative;

    @include media($screen-small) {
      min-height: rem(250);
      padding-top: rem(60);
    }

  }

  &__category {
    @include fill-parent;
    color: $peach;
    display: inline-block;
    font-size: rem(16);
    font-weight: 100;
    text-align: center;
  }

  &__title {
    @include fill-parent;
    color: $light;
    display: inline-block;
    font-size: rem(70);
    font-weight: 900;
    text-align: center;

    @include media($screen-small) {
      font-size: rem(30);
    }
  }

  &__bar {
    @include position(absolute, null null 0 null);
    @include fill-parent;

    @include media($screen-small) {
      position: static;
    }

  }

  &__content {
    @include span-columns(12);
    background: $light;
    margin-top: rem(100);

    @include media($screen-small) {
      margin-top: rem(40);
    }

    &--bottom {
      margin-bottom: rem(60);
    }
  }

  &__text {
    color: $oxford-blue;
    font-size: rem(25);
    font-weight: 300;
    padding: 0 rem(50);
    text-align: center;

    @include media($screen-small) {
      font-size: rem(16);
      padding: 0 rem(30);
    }

  }

  &__images {
    @include span-columns(12);
    background: $light;
    margin-top: rem(60);
    padding: rem(60) 0;

    @include media($screen-small) {
      margin-top: rem(20);
      padding: rem(20) 0;
    }

    figure {
      margin-bottom: rem(40);
    }
  }
}


.bar-case {
  @include outer-container();
  position: relative;

  &__wrapper {
    @include fill-parent;
    @include position(absolute, null null -50px null);
    background: $light;
    border-bottom: 1px solid $oxford-blue;
    color: $grayy;
    font-size: rem(16);
    font-weight: 200;
    line-height: rem(45);
    padding: rem(30) rem(40) rem(20);

    @include media($screen-small) {
      float: left;
      font-size: rem(16);
      padding: rem(20);
      position: static;
    }
  }

  &__col {
    @include span-columns(3 of 12);

    @include media($screen-small) {
      @include span-columns(12);
    }

    &:nth-child(3) {
      text-align: center;
    }
  }

  &__status {
    color: $oxford-blue;
    font-weight: 300;
  }

  &__icon {
    display: inline-block;
    margin-right: rem(20);
  }

  &__icon > svg{
    @include media($screen-medium) {
      margin: 0 rem(10) 0 0;
      @include size(30px);
    }
  }

  &__btn {
    background: url('/assets/images/right-arrow.png') null null null 0 no-repeat;
    color: $oxford-blue;
    float: right;
    font-weight: 300;
    padding-left: rem(50);
    transition: all .2s;

    &:hover {
      background: url('/assets/images/right-arrow.png') null null null 10px no-repeat;
      color: lighten($oxford-blue, 20%);
    }
  }

}


